<template>
  <div class="shopping_content">
    <div class="shoppBox">
      <div class="sortgoods">
        <ul>
          <li class="goodsall"  @click="openEar" :class="{ 'goodshow': optear }" >耳机</li>
          <li class="goodsall" @click="openSpeaker" :class="{ 'goodshow': optSpeaker }">扬声器</li>
        </ul>
      </div>
      <div class="goodlist">
        <ul>
          <li class="goods" v-for="(val,index) in pagelist">
            <div class="goodimg">
              <img  :src="val.imgsrc">
            </div>
            <div class="goodsName">{{val.earphone_name1}}{{val.earphone_style}} - {{val.color}}</div>
            <div class="goodsMoney">
              <span class="specificNum">￥{{val.money}}</span>
              <span class="goodscar" @click="joincar(val)"><img style="" src="../../static/ximg/joincar.png"></span>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <footerbar></footerbar>
  </div>

</template>

<script>
    import footerbar from '../components/footerbar.vue'
    export default {
        name: '',
        components:{
        footerbar
        },
        data: function () {
            return {
              optear:true,
              optSpeaker:false,
              pagelist:[],
              pageear:[],
              pagespeaker:[],
              addcarList:[]
            }
        },
      created:function () {
        console.log("商城页面" )
        let that=this;
        that.$axios.get("https://www.easy-mock.com/mock/59f6c50255aad24ebb7e92f3/example/ear").then((rep) => {
          console.log(rep.data.earlist);
          that.pagelist=rep.data.earlist;
          that.pageear=rep.data.earlist;

        }).catch(function(error){
          console.log(eeror)
        })
      },
      mounted:function () {
        let that=this;
        that.$axios.get("https://www.easy-mock.com/mock/59f6c50255aad24ebb7e92f3/example/speaker").then((rep) => {
          console.log(rep.data.speakerlist);
          that.pagespeaker=rep.data.speakerlist;
        }).catch(function(error){
          console.log(eeror)
        })
      },
      methods:{
        openSpeaker:function(){
          console.log("扬声器")
          this.optear=false;
          this.optSpeaker=true;
          this.pagelist=this.pagespeaker;
        },
        openEar:function(){
          console.log("耳机")
          this.optear=true;
          this.optSpeaker=false;
          this.pagelist=this.pageear;
        },
        joincar:function(item){
          let that=this;
          let carLength=that.addcarList.length;
          console.log(carLength);
          if(!carLength){
            console.log("数组为空");
            that.addcarList.push(item);
          }else{
            console.log("数组不为空");
            let  identical=false
            for(let i=0; i<carLength;i++){
              if(that.addcarList[i].id==item.id) {
                ++that.addcarList[i].goodsCount;
                    identical=true;
                     break;
              }
            }
            if(!identical){
              console.log(identical+"没有遍历到相同ID数值")
              that.addcarList.push(item);
            }
          }
          console.log(that.addcarList)
          var str = JSON.stringify(that.addcarList);
          localStorage.setItem("options",str);
        }
      }
    }
</script>

<style scoped>
  .shopping_content{
    padding-bottom:1.7rem;
  }
  .shoppBox{
    width: 100%;
    height: 100%;
    background-color: #f7f7f8;
    font-size: 0.44rem;
  }
 .sortgoods{
   width: 100%;
   height: 100%;
   overflow: hidden;
   background-color: white;
 }
 .goodsall{
   float: left;
   width: 50%;
   height:1.2rem;
   line-height:1.2rem;
   font-size:0.48rem;
 }
  .goodshow{
    color: #e01e3c;
    border-bottom:2px solid #e01e3c;
  }
  .goodlist{
    overflow: hidden;
    margin-bottom: 10px;
  }
  .goods{
     margin:5px 1% 0px;
     height:6rem;
     width:48%;
     float: left;
     background-color: white;
     position: relative;
  }
  .goodimg{
    width: 100%;
    height:3.5rem;
  }
  .goodimg img{
    height: 100%;
  }
  .goodsName{
   height:1.5rem;
    line-height: 0.7rem;
  }
  .goodsMoney{
    height:1rem;
    line-height: 1rem;
  }
  .goodscar{
    float: right;
    display: block;
    width:0.7rem;
    height: 100%;
    margin-right:3%;
  }
  .specificNum{
    float: left;
    margin-left:3%;
    color:#e01e3c;
    font-weight: 600;
  }
  .goodscar img{
    width: 100%;
    vertical-align:middle;
  }
</style>



