<template>
    <div>
      <!--<div class="" style="text-align: center;background-color: black;height:50px;padding-top:2px;">
        <img src="../assets/logo2.png" width="25px" height="25px" style="vertical-align: middle;">
      </div>-->
      <div class="back_bar">
        <div class="backIcon"><a href="#"><img src="../assets/backicon2.png"></a></div>
        <div class="text">{{imgname1}}<sup style="color: red;" v-if="imgname2">{{imgname2}}</sup><span v-if="imgname3">{{imgname3}}</span>- RMB 2,888</div>
      </div>
      <div class="showBox1">
          <div class="showTitle">
              <h2>{{imgname1}}<sup style="color: red;" v-if="imgname2">{{imgname2}}</sup><span v-if="imgname3">{{imgname3}}</span></h2>
              <div class="showMoney">RMB 2,888</div>
          </div>
          <div class="showImg">
             <img  :src="isrc1">
          </div>
      </div>
      <div class="showdepict">{{imgname1}}<sup>{{imgname2}}</sup><span v-if="imgname3">{{imgname3}}</span>  {{alltext}}</div>
      <div class="show_each">

      </div>
      <div class="buyFold">
         <ul>
           <li class="buyFooter borderright"  @click="goodsCollect"><img  :src="collectimg" alt="收藏"></li>
           <li class="buyFooter"  @click="goodsCar"><img :src="carimg" alt="购物车"></li>
           <li class="buyFooter buyFooter_bg2">加入购物车</li>
           <li class="buyFooter buyFooter_bg">立即购买</li>
         </ul>
      </div>
    </div>
</template>

<script>
    export default {
        name: '',
        data: function () {
            return {
              collectimg:"/static/ximg/collect.png",
              collectstate:false,
              carimg:"/static/ximg/joincar.png",
              imgname1:'',
              imgname2:'',
              imgname3:'',
              imgcolor:'',
              isrc1:'',
              alltext:""
            }
        },
      created:function () {
        console.log("详情页面" )
        this.imgcolor = this.$route.query.imgcolor;
        this.imgname1 = this.$route.query.imgname1;
        this.imgname2 = this.$route.query.imgname2;
        this.imgname3 = this.$route.query.imgname3;
      },
      mounted: function (){
        let that=this;
        that.$axios.get('https://www.easy-mock.com/mock/59f6c50255aad24ebb7e92f3/example/query',{params:{name:that.imgname2,textsrc:that.imgcolor}}).then((rep) => {
              console.log(rep.data);
          that.isrc1=rep.data.data.name.isrc;
          that.alltext=rep.data.data.name.alltext;
          }).catch(function(error){
            console.log(error)
        })
      },
      methods:{
        goodsCollect:function (){
          if(this.collectstate){
            this.collectimg="/static/ximg/collect.png";
            this.collectstate=false;
          }else{
            this.collectimg="/static/ximg/collect1.png"
            this.collectstate=true;
          }
        },
        goodsCar:function (){
          this.$router.push({path:"/car"})
        }
      }
    }
</script>

<style scoped>
  img{
    margin-top: 0;
    padding-top: 0;
  }
.back_bar{
  position: fixed;
  z-index: 900;
 /* top: 50px;*/
  left: 0;
  width: 100%;
  height: 50px;
  border: solid #e5e5e5;
  border-width: 1px 0;
  background-color: #fff;
}
  .backIcon{
    position: absolute;
    width: 52px;
    height: 100%;
    border-right: 1px solid #e5e5e5;
  }
  .backIcon a{
    display: block;
    width:20px;
    height:20px;
    margin:0 auto;
    margin-top: 15px;
  }
  .backIcon a img{
    width: 100%;
    display: block;
  }
  .text{
    text-align: center;
    line-height: 48px;
    font-size: 0.44rem;
    font-weight:700;
    color: #2e3030;
  }
  .showBox1{
    width: 100%;
  /*  height:12rem;*/
    top: 50px;
    position: relative;
    /*border:1px solid red;*/
    background-color: #f4f4f4;
  }
  .showTitle{
     text-align: center;
     padding-top:0.5rem;
  }
  .showTitle h2{
    color: #222021;
    font-size:0.56rem;
    padding-bottom:0.2rem;
  }
  .showMoney{
    color: #222021;
    font-size:0.44rem;
  }
  .showImg{
    width: 100%;
    padding-bottom:0.5rem;
  }
  .showImg img{
    width: 100%;
  }
  .showdepict{
   /* position: absolute;*/
    font-size: 0.44rem;
    width: 70%;
    padding:80px 15% 30px;
    color:#222021;
    font-weight:500;
    text-align: center;
  }
  .show_each{
    background-color: #f4f4f4;
    width: 100%;
    height: 6rem;
    position: absolute;
  }
  .buyFold{
    position:fixed;
    bottom:0;
    left: 0;
    width: 100%;
    height: 1.6rem;
    background-color: white;
    border-top: 1px solid #ccc;
  }
  .buyFooter{
    width:25%;
    height: 100%;
    color: #888;
    font-size:0.14rem;
    line-height: 1.6rem;
    text-align: center;
    text-decoration: none;
    float: left;
  }
  .buyFooter img{
     width:20px;
     height: 20px;
    vertical-align: middle;
  }
  .buyFooter_bg{
    background-color:#e01e3c;
    color: white;
  }
  .buyFooter_bg2{
    background-color: #e0585c;
    color: white;
  }
  .borderright{
    box-sizing: border-box;
    border-right: 1px solid #ccc;
  }
</style>



