<template>
    <div>
      <div class="" style="text-align: center;background-color: black">
        <img src="../assets/logo2.png" width="25px" height="25px">
      </div>
      <div class="bannerBox">
         <img src="../assets/banner1.jpg">
      </div>
      <div class="text_describe">
        <div class="text">
          <p>Beats Studio <sup>3</sup>
            Wireless 现已全新登场，采用完全自适应消噪 (Beats Pure ANC) 技术和 Apple W1 芯片。</p>
        </div>
        <div class="text text_btn">
             <span class="btn"  @click="godetails">了解更多</span>
          <span class="btn btn_bg">去购买</span>
        </div>
      </div>
       <div class="samllTitle">
         <span>精选产品</span>
       </div>
      <div class="swiperBox">
          <div class="swiper-container">
            <div class="swiper-wrapper">
              <div class="swiper-slide" v-for="(item,key) in btwoimgsrc">
                <router-link  :to="{path:'/allDetails',query:{imgcolor:item.color,imgname1:item.earphone_name1,imgname2:item.earphone_name2,imgname3:item.earphone_name3}}">
                  <img class="imgload"  :src="item.myimgsrc" alt="首页banner图片01">
                  <p class="banner_text_desc">{{item.earphone_name1}}<sup>{{item.earphone_name2}}</sup>{{item.earphone_name3}} - {{item.color}}</p>
                </router-link>
              </div>
            </div>
            <div class="swiper-pagination"></div>
          </div>
      </div>
      <div class="spaceOut"></div>
      <div class="swiperBox">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(val, key) in boneimgsrc">
              <router-link  :to="{path:'/allDetails',query:{imgcolor:val.color,imgname1:val.earphone_name1,imgname2:val.earphone_name2}}">
                <img class="imgload"  :src="val.myimgsrc" alt="">
                <p class="banner_text_desc">{{val.earphone_name1}}<sup>{{val.earphone_name2}}</sup> - {{val.color}}</p>
              </router-link>
            </div>
          </div>
          <div class="swiper-pagination"></div>
        </div>
      </div>

      <div class="spaceOut"></div>
      <div class="swiperBox">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(val, key) in bthreeimgsrc">
              <router-link  :to="{path:'/allDetails',query:{imgcolor:val.color,imgname1:val.earphone_name1,imgname2:val.earphone_name2,imgname3:val.earphone_name3}}">
                <img class="imgload"  :src="val.myimgsrc" alt="">
                <p class="banner_text_desc">{{val.earphone_name1}}<sup>{{val.earphone_name2}}</sup>{{val.earphone_name3}} - {{val.color}}</p>
              </router-link>
            </div>
          </div>
          <div class="swiper-pagination"></div>
        </div>
      </div>
      <div class="spaceOut"></div>
      <div class="footerImg">
        <img src="../assets/home_v3.jpg">
      </div>
      <div style="margin-bottom:2.4rem;">
        <div class="text">
          <p>Beats Neighborhood Collection 献给当下和未来的创意先锋，给所有时尚张扬的街头潮人，而他们的灵感正是源于自己生活的城市街头。每条街道都有自己的故事，你也一样。</p>
        </div>
        <div class="text text_btn">
          <span class="btn">了解更多</span>
          <span class="btn btn_bg">去购买</span>
        </div>
      </div>
      <footerbar></footerbar>
    </div>
</template>

<script>
   import footerbar from '../components/footerbar.vue'
   import Swiper from "../../static/swiper-3.4.2.min.js"
    export default {
        name: '',
        components:{
        footerbar
        },
        data: function () {
            return {
              boneimgsrc:[],
              btwoimgsrc:[],
              bthreeimgsrc:[]
            }
        },
      created:function () {
        console.log("实例创建" )
      },
      mounted: function (){
      var   that=this;
        this.$axios.get('https://easy-mock.com/mock/59f6c50255aad24ebb7e92f3/example/index/imgsrc').then((response) => {
          this.boneimgsrc=response.data.bannerOneimg;
          this.bthreeimgsrc=response.data.bannerThreeimg;
          this.btwoimgsrc=response.data.bannerTwoimg;
          console.log(response.data.bannerOneimg);
          console.log("成功");
          that.$nextTick(function () {
            var  mySwiper = new Swiper('.swiper-container', {
              autoplay:1000,
              speed:300,
              direction: 'horizontal',
              pagination: '.swiper-pagination'
            });
          })
        }).catch(function(error){
          console.log(error)
        })
   },
      methods:{
         godetails:function(){
          this.$router.push({path:'/car'})
        }
      }
   }
</script>

<style scoped>
  @import url("../assets/swiper-3.4.2.min.css");
  .bannerBox{
    width:100%;
    height:8.5rem;
  }
  .bannerBox>img,.footerImg>img{
    width: 100%;
    height:100%;
  }
  .footerImg{
    width:100%;
    height:6rem;
  }
  .text_describe{
    padding-bottom:1.5rem;
  }
  .text{
    font-size: 0.44rem;
    width: 70%;
    padding:1rem 15% 0;
    color:#222021;
    font-weight:500;
    text-align: center;
  }
  .btn{
    padding:0.25rem 0.5rem;
    border-radius:0.5rem;
    box-shadow: inset 0 0 0 2px #d3d2d2;
  }
  .btn_bg{
    background:#e01e3c;
    color:white;
    box-shadow: none;
  }
  .samllTitle{
    text-align: left;
    background-color: #f7f7f8;
    padding:0.5rem 0;
  }
  .spaceOut{
    text-align: left;
    background-color: #f7f7f8;
    padding:0.15rem 0;
  }
  .samllTitle>span{
    display: block;
    width: 90%;
    margin: 0 auto;
    color: #222021;
    font-weight: 700;
    font-size:0.54rem;
  }
  .swiper-container,.swiper-slide{
    width: 100%;
    height:10rem;
  }
  .swiper-slide img{
    width:90%;
    height:90%;
  }
  .swiperBox{
    margin-bottom:0.5rem;
  }
  .swiper-container-horizontal>.swiper-pagination-bullets{
    bottom:0px;
  }
  .banner_text_desc{
    text-align: center;
    height: 42px;
    line-height: 42px;
    position: absolute;
    left: 0;
    bottom:7%;
    right: 0;
    font-size: 16px;
    font-weight: 600;
    color: #3c393b;
    background-color: rgba(0,0,0,0);
    background: -moz-linear-gradient(top,rgba(0,0,0,0),rgba(0,0,0,0.7));
    background: -webkit-linear-gradient(top,rgba(0,0,0,0),rgba(0,0,0,0.7));
    background: -o-linear-gradient(top,rgba(0,0,0,0),rgba(0,0,0,0.7));
  }
</style>



